  .card {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
  
  .clear {
    background: #f7f7f7;
    border-width: 0;
    font-size: 13px;
    color: blue;
    margin-left: 20px;
  }
  
  .clear:focus {
    outline: none;
  }
  
  /* .container {
    margin: 40px auto;
    width: 300px;
  } */
  
  .error {
    color: red;
    font-size: 10px;
  }
  
  .input {
    border-radius: 20px;
    border: 1px solid #bbb;
    box-sizing: border-box;
    font-size: 18px;
    height: 40px;
    margin-top: 20px;
    padding: 2px 5px;
    width: 350px;
  }
  
  .input:focus {
    outline: none;
  }
  
  .label {
    font-weight: 900;
  }
  
  .search {
    display: flex;
    flex-direction: column;
    margin: 0 auto 20px auto;
  }
  
  .spinner {
    animation: spin 2s linear infinite;
    border-radius: 50%;
    border: 16px solid #e3e3e3;
    border-top: 16px solid #ee1515;
    box-sizing: border-box;
    height: 120px;
    margin: 60px auto;
    width: 120px;
  }
  
  .sprites {
    display: inline-block;
    margin: 20px auto 0 auto;
    width: 300px;
  }
  
  @keyframes spin {
      0% { transform: rotate(0deg); }
      100% { transform: rotate(360deg); }
  }
